.lightbox {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    width: 75%;
    max-width: 500px;
    height: 60vh;
    top: 20vh;
    left: 50%;
    margin-left: -37.5%;
    padding: 1em 2em;
    text-align: center;    
    -webkit-animation-name: pop-lightbox;    
            animation-name: pop-lightbox;
}
@-webkit-keyframes pop-lightbox {
    from{
        width: 0%;
        height: 0%;
        margin-left: 0px;
        top: 50%;
        opacity: 0;
    }
    to{
        width: 500px;
        height: 60vh;
        top: 20vh;
        margin-left: -250px;
        opacity: 1;
    }
}
@keyframes pop-lightbox {
    from{
        width: 0%;
        height: 0%;
        margin-left: 0px;
        top: 50%;
        opacity: 0;
    }
    to{
        width: 500px;
        height: 60vh;
        top: 20vh;
        margin-left: -250px;
        opacity: 1;
    }
}

.cancel-container {
    -ms-flex-item-align: center;
        align-self: center;
    margin: auto;
}

.textbox {
    margin-bottom: 1em;
    font-size: 0.75em; 
}

.button {
    padding: 0.5em 0.75em;
    margin: 0.5em;
}
.confirm {
    color: #FFF;
    font-weight: 600;
    background: -webkit-gradient(linear,left top, left bottom,from(#ff0000),to(#ff2d2d));
    background: -webkit-linear-gradient(#ff0000,#ff2d2d);
    background: -o-linear-gradient(#ff0000,#ff2d2d);
    background: linear-gradient(#ff0000,#ff2d2d);
    border: 1px solid #ff0000;

    -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
} .confirm:active {
    background: -webkit-gradient(linear,left top, left bottom,from(#d60000),to(#e42929));
    background: -webkit-linear-gradient(#d60000,#e42929);
    background: -o-linear-gradient(#d60000,#e42929);
    background: linear-gradient(#d60000,#e42929);
}

@media (min-width: 600px){
    .lightbox {
        width: 500px;
        left: 50%;
        height: 20em;
        top: 35vh;
        margin-left: -250px;
        -webkit-animation-name: pop-lightbox;
                animation-name: pop-lightbox;
    }
    .textbox {
        margin-bottom: unset;
    }
    .button {
        padding: 0.5em;
        margin: 2em 1em;
    }
    @-webkit-keyframes pop-lightbox {
        from{
            width: 0%;
            height: 0%;
            margin-left: 0px;
            top: 50%;
            opacity: 0;
        }
        to{
            width: 500px;
            height: 20em;
            margin-left: -250px;
            top: 35vh;
            opacity: 1;
        }
    }
    @keyframes pop-lightbox {
        from{
            width: 0%;
            height: 0%;
            margin-left: 0px;
            top: 50%;
            opacity: 0;
        }
        to{
            width: 500px;
            height: 20em;
            margin-left: -250px;
            top: 35vh;
            opacity: 1;
        }
    }
}


